import React from "react";
import { NavLink } from "react-router-dom";
import styled from 'styled-components'
import Logo from './Logo'

interface TopMenuProps {
    isSticky: boolean,
    distanceFromTop: number
}

const MobileTopMenu: React.FC<TopMenuProps> = ({ isSticky, distanceFromTop }) => {
    return (
        <StyleTopMenu isSticky={isSticky} distanceFromTop={distanceFromTop}>
            <StyledContainer>
                <StyledTopMenuInner isSticky={isSticky} distanceFromTop={distanceFromTop}>
                    <div style={{ flex: 1, display: 'grid', gridAutoFlow: 'column', width: "90%" }}>
                        <div style={{ justifySelf: 'start', alignSelf: 'center' }}>
                            <Logo />
                        </div>
                        {/* <StyledLaunchLink exact activeClassName="active" to="/mamms">Launch App</StyledLaunchLink> */}
                        <StyledLaunchLink href="https://twitter.com/TiTiProtocol" target="_blank">Launch App</StyledLaunchLink>
                    </div>
                    <StyledNav>
                        <StyledLink href="https://docsend.com/view/wbknd84pehb7vxs2" target="_blank">Whitepaper</StyledLink>
                        <StyledLink href="https://docs.titi.finance/" target="_blank">Docs</StyledLink>
                        <StyledLink href="https://docsend.com/view/v9s26v6r4bb2vf8s" target="_blank">Deck</StyledLink>
                        <StyledLink href="https://blog.titi.finance/" target="_blank">Blog</StyledLink>
                        <StyledLink href="https://discord.gg/fWyj7rwvR3" target="_blank">Community</StyledLink>
                    </StyledNav>
                </StyledTopMenuInner>
            </StyledContainer>
        </StyleTopMenu>
    )
}

interface StyledTopMenuProps {
    isSticky: boolean,
    distanceFromTop: number
}

const StyleTopMenu = styled.div<StyledTopMenuProps>`
    position: absolute;
    z-index: 99;
    width: 100%;
    background-color: ${props => props.distanceFromTop < -150 ? props.theme.stickyTopBarBG : props.theme.topBarBG};
    opacity: 0.9;
    transition: all 0.3s;
`

const StyledContainer = styled.div`
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1000px;
    padding: 0 10px;
    width: 100%;
`
const StyledTopMenuInner = styled.div<StyledTopMenuProps>`
    align-items: center;
    display: flex;
    height: ${props => props.distanceFromTop < -150 ? props.theme.stickyTopBarSize : props.theme.topBarSize}px;
    justify-content: space-between;
    max-width: ${props => props.theme.siteWidth}px;
    width: 100%;
    flex-wrap: wrap;
    transition: all 0.3s;
    
    @media (max-width: 768px) {
        display: flex;
        justify-content: center;
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        height: 80px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
`
const StyledNav = styled.nav`
    grid-auto-flow: column;
    column-gap: ${props => props.theme.spacing[5]}px;
    display: grid;
    @media (max-width: 768px) {
        column-gap: 12px;
    }
`
const StyledLink = styled.a`
    color: ${props => props.theme.color.grey[100]};
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    transition: all 0.3s;
    border-radius: 8px;
    align-self:center;
    justify-self:center;
    &:hover {
        color: #08F3CB;
    }
`
// const StyledLaunchLink = styled(NavLink)`
const StyledLaunchLink = styled.a`
    color: #08F3CB;
    font-weight: 500;
    font-size: 16px;
    align-self:center;
    justify-self:end;
    padding-top: ${props => props.theme.spacing[1]}px;
    padding-bottom: ${props => props.theme.spacing[1]}px;
    padding-left: ${props => props.theme.spacing[3]}px;
    padding-right: ${props => props.theme.spacing[3]}px;
    text-decoration: none;
    transition: all 0.3s;
    border: 1px solid #08F3CB;
    border-radius: 6px;
    &:hover {
        background-color: #08F3CB;
        color: ${props => props.theme.color.grey[100]};
    }
`
export default MobileTopMenu;
