import React, { useEffect } from 'react';
import styled from 'styled-components'
import JParticles from 'jparticles'
import MobileTopMenu from "./MobileNav";

import coreIntroduce from "../../assets/img/feature/core.png"
import techArch from "../../assets/img/feature/arch.svg"
import roadMap from "../../assets/img/feature/roadmap.svg"
import bg_home from "../../assets/img/background/bg_home.png"

import advantage1 from "../../assets/img/feature/advantage1.svg"
import advantage2 from "../../assets/img/feature/advantage2.svg"
import advantage3 from "../../assets/img/feature/advantage3.svg"
import Spacer from "../../components/Spacer";
import whitelogo from "../../assets/img/icon/whiteLogo.svg";
import titiToken from "../../assets/img/icon/whiteLogo.svg"
import tiusdToken from "../../assets/img/icon/whiteTiUSD.svg"
import { Sticky, StickyContainer } from 'react-sticky';

const Home: React.FC = () => {

    useEffect(() => {
        new JParticles.Wave('#wave', {
            num: 3,
            // 不绘制边框
            line: true,
            lineColor: [
                'rgba(0, 190, 20, 0.5)',
                'rgba(0, 190, 112, 0.7)',
                'rgba(0, 190, 112, 0.9)',
            ],
            lineWidth: [1, 2, 3],
            offsetTop: 0.75,
            offsetLeft: [0.5, 0.7, 0.9],
            crestHeight: [10, 14, 18],
            // 填充
            fill: false,
            // 填充颜色
            fillColor: '#27C9E5',
            crestCount: 3,
            speed: 0.04,
        });

    })


    return (
        <><StyledPage>
            <StickyContainer>
                <Sticky topOffset={0}>
                    {({ style, isSticky, distanceFromTop }) => (
                        <div style={style}>
                            <MobileTopMenu isSticky={isSticky} distanceFromTop={distanceFromTop} />
                        </div>
                    )}
                </Sticky>

                <MainHeader>
                    <HeaderWrapper>
                        <HomeHeader>
                            <p style={{ margin: "200px 0 0 0" }}>Multi-Asset Reserves <span style={{ color: "#08F3CB" }}>Stablecoin</span></p>
                            <p  style={{ margin: "0" }}>powered by <span style={{ color: "#08F3CB" }}>ReOrders</span></p>
                            <div style={{ marginTop: "80px", fontSize: "24px", color: "#8D8D8D" }}>
                                The First Use-To-Earn Algorithmic Stablecoin
                            </div>

                        </HomeHeader>
                    </HeaderWrapper>
                </MainHeader>
                <div style={{ height: "480px", marginBottom: "-480px", backgroundImage: "linear-gradient(#000,#000,#000,#000,#000,#000,rgba(0,0,0,0.2), rgba(0,0,0,0))" }}></div>
                <StyledDivider></StyledDivider>
                <TitleStyle>Advantages</TitleStyle>
                <CardWrapper width="60%">
                    <FeatureCardWrapper bg={advantage1}>
                        <StyledFeatureText>
                            <div style={{ fontSize: "25px", fontWeight: 600, paddingBottom: "50px" }}>High Stability</div>
                            <div style={{ fontSize: "18px", textAlign: "left", color: "#E5E5E5" }}>100% backed Asset and ReOrders mechanism to ensure high stability while improving capital utilization and risk resistance.</div>
                        </StyledFeatureText>
                    </FeatureCardWrapper>
                    <Spacer />
                    <FeatureCardWrapper bg={advantage2}>
                        <StyledFeatureText>
                            <div style={{ fontSize: "25px", fontWeight: 600, paddingBottom: "50px" }}>High Liquidity</div>
                            <div style={{ fontSize: "18px", textAlign: "left", color: "#E5E5E5" }}>Through the original MAMM-based currency issuance and market regulation model, it provides great liquidity for funds.</div>
                        </StyledFeatureText>
                    </FeatureCardWrapper>
                    <Spacer />
                    <FeatureCardWrapper bg={advantage3}>
                        <StyledFeatureText>
                            <div style={{ fontSize: "25px", fontWeight: 600, paddingBottom: "50px" }}>High Profitable</div>
                            <div style={{ fontSize: "18px", textAlign: "left", color: "#E5E5E5" }}>The Use-To-Earn method allows users to make profits immediately after use, which enhances their competitive advantage.</div>
                        </StyledFeatureText>
                    </FeatureCardWrapper>
                </CardWrapper>
                <StyledDivider></StyledDivider>
                <TitleStyle>Tokens</TitleStyle>
                <CardWrapper width="60%">
                    <TokenWrapper>
                        <StyledCore2Text>
                            <img src={tiusdToken} alt="feature2" height="80px" width="80px" style={{ padding: "5px 0 30px" }} />
                            <div style={{ fontSize: "25px", fontWeight: 600, paddingBottom: "15px", color: "#08F3CB" }}>TiUSD - Stablecoin Peg $1</div>
                            <div style={{ paddingTop: "15px" }}>TiUSD Token is a visionary multi-asset reserves Stablecoin powered by ReOrders.</div>
                        </StyledCore2Text>
                    </TokenWrapper>
                    <Spacer size='lg' />
                    <TokenWrapper>
                        <StyledCore2Text>
                            <img src={titiToken} alt="feature2" height="80px" width="80px" style={{ padding: "5px 0 30px" }} />
                            <div style={{ fontSize: "25px", fontWeight: 600, paddingBottom: "15px", color: "#08F3CB" }}>TiTi - Governance Token</div>
                            <div style={{ paddingTop: "15px" }}>TiTi  is the protocol governance token that are used to ensure on-chain governance,  and carry the long-term growth value of the protocol.</div>
                        </StyledCore2Text>
                    </TokenWrapper>
                </CardWrapper>
                <StyledDivider></StyledDivider>
                <TitleStyle>How <span style={{ color: "#08F3CB" }}>TiTi-Protocol</span> Works</TitleStyle>
                <CardWrapper width="60%">
                    <ArchTotalWrapper>
                        <div style={{ color: '#FFF', fontSize: '18px', width: '100%' }}>
                            <p style={{ marginBottom: "0px" }}>TiTi Protocol aims to bring a new type of elastic supply algorithm stablecoin solution to DeFi and Web3 that incorporates the <span style={{ color: "#08F3CB" }}>Multi-Asset Reserves</span> mechanism.</p>
                            <p style={{ marginBottom: "0px" }}>TiTi Protocol always monitors changes in the total value of reserve to calculate the average price of TiUSD in circulation and adjusts the market-making peg price of TiUSD in the primary market through the <span style={{ color: "#08F3CB" }}>ReOrders</span> mechanism.</p>
                        </div>
                        <ArchWrapper>
                            <img src={coreIntroduce} alt="feature2" width="100%" />
                        </ArchWrapper>
                    </ArchTotalWrapper>
                </CardWrapper>
                <StyledDivider></StyledDivider>
                <TitleStyle>Technical Core</TitleStyle>
                <CardWrapper width="60%">
                    <InnovationWrapper>
                        <TechCardWrapper width="50%" bgColor="#C7F5EB" margin="0px 10px 10px 0px">
                            <CoreCardWrapper>
                                <StyledCoreText>
                                    <div style={{ color: "#08F3CB", fontSize: "22px", fontWeight: 600, paddingBottom: "15px" }}>TiTi AMMs</div>
                                    This is a new design paradigm for algorithmic stablecoins. It is responsible for capturing the real-time supply and demand relationship of TiUSD and dynamically adjusting the circulation of TiUSD.
                                </StyledCoreText>
                            </CoreCardWrapper>
                        </TechCardWrapper>
                        <TechCardWrapper width="50%" bgColor="#C7F5EB" margin="0px 0px 10px 10px">
                            <CoreCardWrapper>
                                <StyledCoreText>
                                    <div style={{ color: "#08F3CB", fontSize: "22px", fontWeight: 600, paddingBottom: "15px" }}>ReOrders</div>
                                    This is the core mechanism for controlling TiUSD's price to remain stable in TiTi Protocol. It is built on CFMM, which greatly reduces gas consumption while realize price stability with utmost efficiency.
                                </StyledCoreText>
                            </CoreCardWrapper>
                        </TechCardWrapper>
                        <TechCardWrapper width="50%" bgColor="#C7F5EB" margin="10px 10px 0px 0px">
                            <CoreCardWrapper>
                                <StyledCoreText>
                                    <div style={{ color: "#08F3CB", fontSize: "22px", fontWeight: 600, paddingBottom: "15px" }}>Market Maker Fund</div>
                                    A decentralized fund product designed by TiTi Protocol to enhance the protocol's ability to resist volatility risks by introducing the gaming relationship among the algorithmic stablecoin stakeholders.
                                </StyledCoreText>
                            </CoreCardWrapper>
                        </TechCardWrapper>
                        <TechCardWrapper width="50%" bgColor="#C7F5EB" margin="10px 0px 0px 10px">
                            <CoreCardWrapper>
                                <StyledCoreText>
                                    <div style={{ color: "#08F3CB", fontSize: "22px", fontWeight: 600, paddingBottom: "15px" }}>Use-To-Earn</div>
                                    Use-To-Earn refers to that stablecoin users can earn extra revenue by using Stablecoin $TiUSD. The core is to incentivize behaviors with TiUSD that are in the benefit of the both protocol and users.
                                </StyledCoreText>
                            </CoreCardWrapper>
                        </TechCardWrapper>
                    </InnovationWrapper>
                </CardWrapper>
                <StyledDivider></StyledDivider>
                <TitleStyle>RoadMap</TitleStyle>
                <CardWrapper width="100%">
                    <img src={roadMap} alt="feature2" width="100%" style={{ paddingBottom: '50px' }} />
                </CardWrapper>
                <StyledFooter>
                    <FooterWrapper width="60%">
                        <CoreCardWrapper>
                            <StyledLogo>
                                <img src={whitelogo} height="45" alt="NFT" style={{ marginTop: -4, paddingRight: "5px" }} />
                                <StyledLogoText>TiTi PROTOCOL</StyledLogoText>
                            </StyledLogo>
                            <div style={{ color: "#FFF" }}>@TiTi.Protocol. All rights Reserved.</div>
                        </CoreCardWrapper>
                        <MediumWrapper>
                            <StyledMediumLink href="https://twitter.com/TiTiProtocol">
                                Twitter
                            </StyledMediumLink>
                            <StyledMediumLink href="https://discord.gg/fWyj7rwvR3">
                                Discord
                            </StyledMediumLink>
                            <StyledMediumLink href="https://blog.titi.finance/">
                                Medium
                            </StyledMediumLink>
                            <StyledMediumLink href="https://github.com/TiTi-Finance/TiTi-Protocol">
                                Github
                            </StyledMediumLink>
                            <StyledMediumLink href="https://t.me/tiusd_channel">
                                Telegram
                            </StyledMediumLink>
                        </MediumWrapper>
                    </FooterWrapper>
                </StyledFooter>

            </StickyContainer>
        </StyledPage >
        </>
    );
};

const StyledPage = styled.div`
    background-image: url(${bg_home});
    background-repeat: repeat-y;
    position:absolute;
    z-index:-1;
`
const StyledSection = styled.div`
    width:100%;
    height:100vh;
    overflow:hidden;
    @media (max-width: 768px) {
        width:100%;
        height:auto;
        overflow-y:auto;
        overflow-x:hidden;
    }
`

const StyledMain = styled.div`
    align-items: center;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - ${props => props.theme.topBarSize * 2}px);
    background-image: url(${bg_home});
    
`

const StyledIcons = styled.div`
    align-items: center;
    display: flex;
    justify-content: center;
`

const StyledIcon = styled.a`
    align-items: center;
    outline:none;
    display: flex;
    justify-content: center;
    font-weight: 500;
    text-decoration: none;
    text-align: center;
    padding-top: 50px;
    color: #FFF;
    transition: all 0.3s;
    font-size: 25px;
    pointer-events: undefined;
    cursor: pointer;
    &:hover {
        transform: scale(1.06, 1.06);
    }
`

interface CardWrapperProps {
    width?: string;
    isBorder?: boolean;
    bgColor?: string;
    margin?: string;
}

const HeaderWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    width: ${props => props.width};
    text-align: center;
    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
    }
`;

const SectionWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    flex-flow: column nowrap;
    align-items: center;
    width: 100%;
    border-bottom: 0px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background-color: transparent;
`;

const InnovationWrapper = styled.div<CardWrapperProps>`
    margin-top: 0px;
    display: block;
    width: 60%;
    border-bottom: 0px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background-color: transparent;
`;


const TechCardWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    margin: ${props => props.margin};
    width: calc( 50% - 20px );
    float:left;
    border-bottom: 5px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background: black linear-gradient(90deg,#1f1f1f,#000);
    border: 5px solid #000;
    border-radius: 12px;
    transition: all .3s;
    border: 5px solid #027561;
    &:hover {
        border: 5px solid #08F3CB;
    }
    
    @media (max-width: 768px) {
        width:100%;
        margin: 10px 0;
    }
`;

