const MainHeader = styled.div`
    background-color: #000;
    background-size: cover;
    background-repeat: no-repeat;
    height:100vh;
    color: #FFF;
`;

const HomeHeader = styled.div`
    padding-top: 200px;
    justify-content: center;
    height: 200px;
    padding-bottom: 0px;
    font-size: 56px;
    display: flex;
    align-items: center;
    text-align: center;
    width: 60%;
    font-size: 50px;
    font-weight: 500;
    flex-direction: column;
    @media (max-width: 768px) {
        padding-top: 160px;
        width: 80%;
        font-size: 36px;
        display: flex;
        align-items: center;
        text-align: center;
    }
`;

const CoreCardWrapper = styled.div`
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin:0 24px;
    @media (max-width: 768px) {
        width: 80%;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1;
        flex-direction: column;
    }
`;

const ArchTotalWrapper = styled.div<CardWrapperProps>`
    margin: 0 10%;
    border: 1px solid #08ecbd;
    padding: 18px;
    display: flex;
    justify-content: center;
    flex-flow: column nowrap;
    align-items: center;
    height:100%;
    background-color: ${props => props.bgColor};
    border-radius:16px;
`;

const ArchWrapper = styled.div`
    padding: 24px 0;
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    align-items: center;
    

    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1;
        flex-direction: column;
    }
`;

interface FeatureCardWrapperProps {
    bg?: string;
}

const FeatureCardWrapper = styled.div<FeatureCardWrapperProps>`
    margin-top: 38px;
    padding-top: 38px;
    width: 90%;
    flex: 1;
    align-items: center;
    text-align: center;
    height: 400px;
    background-image: url(${props => props.bg});
    -moz-background-size: 100%;
    background-size: 80%;
    background-repeat: no-repeat;
    background-position: bottom center;
    border: 5px solid #027561;
    border-radius: 12px;
    transition: all .3s ease;
    &:hover {
        border: 5px solid #08F3CB;
    }
    @media (max-width: 768px) {
        width: 80%;
        align-items: center;
        flex: 1;
    }
`;

const TokenWrapper = styled.div<FeatureCardWrapperProps>`
    margin-top: 0px;
    margin-bottom: 20px;
    padding-top: 0px;
    width: 100%;
    flex: 1;
    align-items: center;
    text-align: left;
    height: 50vh;
    border: 5px solid #027561;
    border-radius: 12px;
    transition: all .3s ease;
    &:hover {
        border: 5px solid #08F3CB;
    }
    
    @media (max-width: 768px) {
        width: 80%;
        align-items: center;
        flex: 1;
    }
`;

const StyledCoreText = styled.div`
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
    text-align: left;
    font-size: 18px;
    font-weight: 400;
    border-radius: 12px;
    border: 0px solid #F0B90B;
    color: #E5E5E5;
    line-height: 1.46em;
    min-height:180px;
    height:100%;
`

