//`;

const StyledFeatureText = styled.div`
    margin: 10px 10% 260px;
    width: 80%;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    color: #FFF;
`

const StyledCore2Text = styled.div`
    margin: 10px 10%;
    width: 80%;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    padding: 20px 0px 20px;
    color: #FFF;
    line-height: 1.46em;
`

const StyledCore3Text = styled.div`
    margin: 0px 2px;
    width: 70%;
    text-align: left;
    font-size: 18px;
    font-weight: 400;
    padding: 10px 0px 10px;
    color: #FFF;
    line-height: 1.46em;
`


const StyledFooter = styled.div`
    justify-content: center;
    display: flex;
    padding-top: ${props => props.theme.spacing[5]}px;
    padding-bottom: ${props => props.theme.spacing[5]}px;
    background-image: linear-gradient(180deg,#101010,#000);
`

const StyledLogo = styled.div`
    align-items: center;
    display: flex;
    margin-bottom: 50px;
    @media (max-width: 768px) {
        align-items: center;
        display: flex;
        margin-right: 0;
    }
`;

const StyledLogoText = styled.div`
    color: ${props => props.theme.color.grey[100]};
    text-decoration: none;
    font-size: 26px;
    font-weight: 500;
    margin-left: ${(props) => props.theme.spacing[2]}px;
`;

const StyledMediumLink = styled.a`
    color: ${props => props.theme.color.grey[100]};
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    margin: ${(props) => props.theme.spacing[3]}px;
    transition: all .3s ease;
    &:hover {
        color: #08F3CB;
    }
`;

const MediumWrapper = styled.div`
    padding-top: 38px;
    padding-bottom: 38px;
    display: flex;
    justify-content: center;
    width: 100%;
    align-items: center;

    @media (max-width: 768px) {
        width: 80%;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1;
        flex-direction: column;
    }
`;

const TitleStyle = styled.div`
    margin-top: 90px;
    margin-bottom: 20px;
    font-size: 46px;
    font-weight: 600;
    text-align: left;
    color: #08F3CB;

    @media (max-width: 768px) {
        
        font-size: 36px;
        align-items: center;
        text-align: center;
    }
`;

const StyledDivider = styled.div`
    background:linear-gradient(to left,rgba(255,255,255,0),#08F3CB,rgba(255,255,255,0));
    height:1px;
    margin:60px 0;
`;

export default Home;
