import React from 'react';
import styled from 'styled-components';
import logo from "../../assets/img/icon/whiteNavLogo.svg";

const Logo: React.FC = () => {
    return (
        <StyledLogo>
            <img src={logo} height="45" alt="NFT" style={{ marginTop: -4 }} />
        </StyledLogo>
    );
};

const StyledLogo = styled.div`
    align-items: center;
    display: flex;
    margin-right: 260px;
    @media (max-width: 768px) {
        align-items: center;
        display: flex;
        margin-right: 0;
    }
`;

export default Logo;
